import React from 'react'
import { Link } from 'react-router-dom'
export const ArticlesList = ({ articles }) => {
 return (
  <div>
   {articles.map((article, key) => (
    <Link className="article-list" key={key} to={`/article/${article.name}`} >
     <h3> {article.title}</h3>
     <p>{article.content[0].substring(0, 100)}...<span>More</span></p>
     <br />
     <hr></hr>
     <br />
    </Link>
   ))}
  </div>
 )
}
